import Cookies from "universal-cookie/es6";

function LogOut(props){
    function close(){
        const cookie = new Cookies();
        cookie.set("user", undefined, {path:"/"});
        window.location.href = "https://sistemaact.github.io/SistemaACTropico/";
    }
return (
    <div id="LoginPopup">
        <h2>¿Deseas cerrar session?</h2>
        <button onClick={close}>Sí</button>
        <br></br>
        <button onClick={()=>{props.closeMenu(false)}}>No</button>
        <br></br>
    </div>
)
}

export default LogOut
