import React, {useState} from "react"
import Confirmacion from "./ConfirmacionAsistencia";



function Empleados(props){

    var date = new Date()
    var key;
    var filter = [];

    if(date.getDay() === 0){
        key = "D";
      }
      else if(date.getDay() === 1){
        key = "L";
      }else if(date.getDay() === 2){
        key = "Ma";
      }else if(date.getDay() === 3){
        key = "Mi";
      }else if(date.getDay() === 4){
        key = "J"
      }else if(date.getDay() === 5){
        key = "V";
      }else if(date.getDay() === 6){
        key = "S"
      }


    
    props.Personal.Asistencia.map((Empleado)=>{

        if(Empleado.Planta === props.Filter && Empleado[key] === ""){
            filter.push(Empleado);
        }
        return null
    });

    
    return (

        <div style={{marginBottom:"110px"}}>

        {filter.map((Empleado)=>{
        return (
            <center key={Empleado.Id}>
            <div className="EmpleadoAsitencia" key={Empleado.Id}>
                <h1>{Empleado.Nombre}</h1> 
                
                <Confirmacion postArray={props.postArray} Empleado={Empleado}></Confirmacion>
                <HorasExtra postObj={props.postObj} Empleado={Empleado.Nombre}></HorasExtra>
            </div>
            </center>
            )
        })}
        </div>
        
    )

    
}

function HorasExtra(props){

  const [Horas, SetHoras] = useState(0)
  const [HorasAgregadas, SetHorasAgregadas] = useState(false)
  function handleChange(e){
      SetHoras(e.target.value)
  }

  function addToObj(){
    if(Horas > 3 || Horas < 0) alert("Tienes que poner entre 3 a 0 horas extra")
    else{
    props.postObj[props.Empleado] = {Nombre:props.Empleado, HorasExtra:Horas};
    SetHorasAgregadas(true)
    }
  }

  const Agregado = () =>{
    if(HorasAgregadas === true) return(<img alt="Yes" style={{width:"50px",marginTop:"0px"}} src="https://image.flaticon.com/icons/png/512/3572/3572255.png"></img>)
    else return null
  }

  return(
    <div>
      <label>Ingresa la cantidad de horas extra   </label>
      <input type="number" max="3" min="0"  onChange={handleChange}></input>
      <p>El numero tiene que ser menor o igual a 3</p>
      <button onClick={addToObj}>Agregar Horas Extra</button><br/><br/>
      <Agregado></Agregado>
    </div>
  )

}

export default Empleados