import React, {useEffect, useState} from "react";
import ArticulosSolicitados from "./NestedComponent/ArticulosSolicitados";
import readXlsxFile from 'read-excel-file'

function Requisicion(props){

    useEffect(() => {
        props.SetOptions("Requisiciones");
        props.postArray.splice(0, props.postArray.lenght); 
        for (var variableKey in props.postObj){
            if (props.postObj.hasOwnProperty(variableKey)){
                delete props.postObj[variableKey];
            }
        } 
    }, [])

   /* const readExcel = (file)=>{
        const promise = new Promise((resolve,reject) =>{
            const fileReader = new FileReader();
            fileReader.readAsArrayBuffer(file)

            fileReader.onload = (e)=>{
                const bufferArray = e.target.result;

                const wb = XLSX.read(bufferArray, {type : "buffer"});

                const wsname = wb.SheetNames[0];

                const ws = wb.Sheets[wsname];

                const data = XLSX.utils.sheet_to_json(ws);

                resolve(data);
            };
            fileReader.onerror = ((error)=>{
                reject(error);
            });
        });

        promise.then((d)=>{
            props.postArray.push(d)
        })
    };
*/
    
    var Permisions = ["Admin", "Ingeniero", "Supervisor"];

    if (props.user !== undefined){
        if(Permisions.indexOf(props.user.Rol) !== -1 ){

            function setFolio(e){
                props.postObj["Folio"] = e.target.value; 
            }

            return(
                <div className="RequisicionForm">

                    <h1>Requisiciónes</h1>
                    <p>Agrega un archivo de requisición. Este debe de estar hecho en el fomrato que se presenta en la siguiente liga</p>
                    <a target="_blank" href="https://docs.google.com/spreadsheets/d/1shVvi4SEaW_9bQatZdDFxiGuPhFVixZRWjxNJEQR_us/edit?usp=sharing" style={{color:"blue"}}>https://docs.google.com/spreadsheets/d/1shVvi4SEaW_9bQatZdDFxiGuPhFVixZRWjxNJEQR_us/edit?usp=sharing</a>
                    <p>Para editar el documento favor de descargarlo como xlsm</p>
                    <br/>
                    <label style={{width:"auto", marginBottom:"15px"}}>Ingresa el folio de tu requisición</label>
                    <input style={{width:"auto"}} type="text" onChange={setFolio} ></input>
                    <br/>
                    <br/>
                    <center><input style={{width:"auto"}} type="file" onChange={(e)=>{
                        const file = e.target.files[0];
                        readXlsxFile(file).then((rows) => {
                            console.log(rows)
                            props.postArray.push(rows)
                            // `rows` is an array of rows
                            // each row being an array of cells.
                          })

                    }}></input>
                    </center>
                </div>
            )

        }else if(Permisions.indexOf(props.user.Rol) === -1 ){

            alert("No cuentas con los permisos necesarios")

            return null

        }
    }
    else if(props.user === undefined){
        alert("Necesitas iniciar sesion para continuar")
        return null
    }

}

export default Requisicion