import React, {useState, useEffect} from "react"
import Aprobados from "./nestedComponents/Aprobados"
import Comprados from "./nestedComponents/Comprados"
import Facturados from "./nestedComponents/Facturados"
import Comparados from "./nestedComponents/Comparados"
import react from "react"

function ComprasRequisiciones(props){

    const [menu, setMenu] = useState(true)
    const [app, setApp] = useState(true)
    const [data, setData] = useState(undefined)

    useEffect(()=>{
        props.SetOptions("AdminRequisicion");
        props.postArray.splice(0, props.postArray.lenght);


        var url="https://script.google.com/macros/s/AKfycbzHF7M6mRxaXFg1KhF1DwAAYk0oshGa4MwKJuiIVFQnNmEzMosiBRQYuSh27tUfKrde/exec";

        fetch(url)
            .then(response => {
            return response.json()
            }).then(data =>{
                

               setData(data);

            })
                

    }, [])

    const MiniApp = (props)=>{
        if(props.app === undefined) return null
        if(props.app === "Aprobados") return <Aprobados data={data}></Aprobados>
        else if(props.app === "Comparados") return <Comparados data={data}></Comparados>
        else if(props.app === "Comprados") return <Comprados data={data}></Comprados>
        else if(props.app === "Facturados") return <Facturados data={data}></Facturados>
        else setApp(undefined); setMenu(false);
    }

    if(props.user === undefined){
        alert("No tienes los permisos necesarios para realizar esta acción")
        window.location.href = "https://sistemaact.github.io/SistemaACTropico/"
    }else{
        var permisos = ["Compras", "Admin"]
        if(permisos.indexOf(props.user.Rol) === -1){
            console.log(permisos.indexOf(props.user.Rol))
            alert("No tienes los permisos necesarios para realizar esta acción")
            window.location.href = "https://sistemaact.github.io/SistemaACTropico/"
        }else{
            if(menu === true){
            return(
                <main style={{display: "flex", flexDirection: "column", alignItems: "center"}}>
                    <div onClick={()=>{setApp("Aprobados"); setMenu(false)}} className="HomeMenuOption">
                        <h2>Aprobados</h2>
                    </div>
                    <div onClick={()=>{setApp("Comparados"); setMenu(false)}} className="HomeMenuOption">
                        <h2>Comparados</h2>
                    </div>
                    <div onClick={()=>{setApp("Comprados"); setMenu(false)}} className="HomeMenuOption">
                        <h2>Comprados</h2>
                    </div>
                    <div onClick={()=>{setApp("Facturados"); setMenu(false)}} className="HomeMenuOption">
                        <h2>Facturados</h2>
                    </div>
                </main>
            )
            }else{
                return(
                    <React.Fragment>
                    <MiniApp app={app}></MiniApp>
                    <div style={{marginBottom:"150px"}}></div>
                    </React.Fragment>
                )
            }
        }
    }
    


}

export default ComprasRequisiciones