import {BrowserRouter as Router, Switch, Route} from "react-router-dom";
import React, {useState} from 'react';
import Cookies from "universal-cookie/es6";
import './App.css';
import Asistencia from "./Components/Asistencia"
import Nav from "./Components/Nav"
import LandingPage from "./Components/LandingPage";
import Directorio from "./Components/Directorio";
import Footer from "./Components/Footer";
import Auth from "./PopUps/Auth";
import Requisicion from "./Components/Requisicion";
import AdminRequisiciones from "./AdminComponents/AdminRequisiciones";
import ComprasRequisiciones from "./Compras/ComprasRequisiciones/ComprasRequisiciones";
import ComprasAsistencia from "./Compras/ComprasAsistencia/ComrpasAsistencia";
import Personal from "./AdminComponents/Personal";



function App() {
  const cookies = new Cookies();



  var postArray = []
  var postObj = {}

  const [Logged, Login] = useState(false)
  const [DisplayOptions, setDisplayOptions] = useState(undefined);


  function HandleAuth(){
    Login(true)
  }
  function closeAuth(){
    Login(false)
  }



  function SetOptions(params){
    setDisplayOptions(params)
  }


  return (
    <Router>
      <Nav setOptions={SetOptions} login={HandleAuth} logged={Logged} user={cookies.get("user")}></Nav>
      <Switch>
        <Route  path="/Asistencia/" component={()=><Asistencia SetOptions={SetOptions} user={cookies.get("user")} postArray={postArray} postObj={postObj}></Asistencia>}></Route>
        <Route  path="/Personal" component={()=><Personal SetOptions={SetOptions} user={cookies.get("user")} postArray={postArray}></Personal>}></Route>
        <Route  path="/Directorio/" component={()=> <Directorio user={cookies.get("user")}></Directorio>}></Route>
        <Route path="/Requisiciones" component={()=> <Requisicion SetOptions={SetOptions} postArray={postArray} user={cookies.get("user")} postObj={postObj}></Requisicion>} ></Route>
        <Route path="/AdminRequisiciones" component={()=> <AdminRequisiciones postArray={postArray} SetOptions={SetOptions} user={cookies.get("user")}></AdminRequisiciones>}></Route>
        <Route path="/RequisicionesCompras" component={()=> <ComprasRequisiciones postArray={postArray} SetOptions={SetOptions} user={cookies.get("user")}></ComprasRequisiciones>}></Route>
        <Route path="/AsistenciaCompras" component={()=> <ComprasAsistencia postArray={postArray} SetOptions={SetOptions} user={cookies.get("user")}></ComprasAsistencia>}></Route>
        <Route  path="/SistemaACTropico"  user={cookies.get("user")} component={()=> <LandingPage user={cookies.get("user")}></LandingPage>}></Route>
        <Route  path="/" exact user={cookies.get("user")} component={()=> <LandingPage user={cookies.get("user")}></LandingPage>}></Route>
        <Route path ="*" component={()=> <LandingPage user={cookies.get("user")}></LandingPage>}></Route>
      </Switch>
      <div style={{marginBottom:"100px"}}></div>
      <Footer postArray={postArray} user={cookies.get("user")} DisplayOptions={DisplayOptions} postObj={postObj} ></Footer>

      {/*These are the websites pop ups */}
      <Auth Loginfunction={Login} closeAuth={closeAuth} logged={Logged} user={cookies.get("user")}></Auth>

    </Router>
  );
}

export default App;
