import React from "react";

function ComprasAsistencia(props){

    if (props.user !== undefined){
        return(
            <React.Fragment>
            <iframe className="AsistenciaSheet" src="https://docs.google.com/spreadsheets/d/e/2PACX-1vRYI7wGlyqqXxpU4cMzOKysN18hAfE8J2V71x5Pb4i1_mUXwyfQGosbO3LKbciAbN-khShpE6xGAcNl/pubhtml?widget=true&amp;headers=false"></iframe>
            <div style={{height:"100px"}}></div>
            </React.Fragment>
        )
    }else{
        alert("Necesitas iniciar sesion para acceder a esta pagina");
        window.location.href = "https://sistemaact.github.io/SistemaACTropico"
    }

}

export default ComprasAsistencia