import React, {useState} from "react";
import ValeCompras from "./Vale"

function Aprobados(props){

    const [vale, setVale] = useState(undefined);

    var dataDisplay =[];
    var x= 0;
    
    if(props.data !== undefined){
    props.data.forEach(Vale => {
        if(Vale.Aprovado === true && x < 50){
            dataDisplay.push(Vale)
            x++
        }
        
    });
    }

    function Status(props){

        const sources = ["https://image.flaticon.com/icons/png/512/3572/3572255.png", "https://image.flaticon.com/icons/png/512/3572/3572260.png"];
        if(props.Vale.Aprovado === true){
            var source = sources[0];
        }else{
            var source = sources[1];
        }

        return(
            <div>
                <img style={{width:"30px",height:"30px"}} src={source}></img>
            </div>
        )
    }
    function StatusComprado(props){

        const sources = ["https://image.flaticon.com/icons/png/512/3572/3572255.png", "https://image.flaticon.com/icons/png/512/3572/3572260.png"];
        if(props.Vale.Comprado === true){
            var source = sources[0];
        }else{
            var source = sources[1];
        }

        return(
            <div>
                <img style={{width:"30px",height:"30px"}} src={source}></img>
            </div>
        )
    }
    function StatusFacturado(props){

        const sources = ["https://image.flaticon.com/icons/png/512/3572/3572255.png", "https://image.flaticon.com/icons/png/512/3572/3572260.png"];
        if(props.Vale.Facturado === true){
            var source = sources[0];
        }else{
            var source = sources[1];
        }

        return(
            <div>
                <img style={{width:"30px",height:"30px"}} src={source}></img>
            </div>
        )
    }

    if(props.data !== undefined){  
        console.log(vale)
        if(vale === undefined){
        return (
        <main id="AprobacionesApp">
            {dataDisplay.map((Vale)=>{
                return(
                        <article className="Vale-Notifications">
                            <div className="ValeContainer">
                            <div className="ValeData">
                                <div>
                                    <h3>Usuario</h3>
                                    <p>{Vale.Usuario}</p>
                                </div>
                                <div>
                                    <h3>Fecha</h3>
                                    <p>{Vale.Fecha}</p>
                                </div>
                                <div>
                                    <h3>Folio</h3>
                                    <p>{Vale.Folio}</p>
                                </div>
                            </div>
                            
                            <div className="StatusContainer">
                                <div>
                                    <h4>Aprovado</h4>
                                    <Status Vale={Vale}></Status>
                                </div>
                                <div>
                                    <h4>Comprado</h4>
                                    <StatusComprado Vale={Vale}></StatusComprado>
                                </div>
                                <div>
                                    <h4>Facturado</h4>
                                    <StatusFacturado Vale={Vale}></StatusFacturado>
                                </div>
                                </div>
                                </div>
            
                            <button onClick={()=>setVale(Vale)} >Modificar</button>
            
                        </article>
                )
            })}
        </main>
        
        )
        }else{
            return(
                <React.Fragment>
                <button onClick={()=>{setVale(undefined)}}>Regresar</button>
                <ValeCompras Vale={vale}></ValeCompras>
                <div id="AdminComentarios"></div>
                <div style={{marginBottom:"100px"}}></div>
                </React.Fragment>
            )
        }    
    }
    else{
        return (<h2>Cargando</h2>)
    }
}

export default Aprobados