import React, {useState, useEffect} from "react"
import SubAppPersonal from "./Personal SubApps/PersonalSubApp";


function Personal(props){

    const [SubApp, setSubApp] = useState(undefined);
    const [Data, setData] = useState(undefined);


        useEffect(()=>{
            props.SetOptions("Asistencia");
            props.postArray.splice(0, props.postArray.lenght);
    
            
            var url = "https://script.google.com/macros/s/AKfycbwAFHxBhFH1M6FxgexrMu1iuQuRa7Qt7Wjf_cg9t3JpRxJzI3XIdGi4zp8PivRmvte5/exec"
            
            fetch(url)
                .then(response => {
                return response.json()
                }).then(data =>{
                
    
                   setData(data);
                    
                })
        },[]);

        function handleClick(params){
            setSubApp(params)
        }


    if(SubApp === undefined){
        if(props.user !== undefined || props.user.Rol === "Admin"){
         return (

            <div className="HomeMenuContainer">

                <div onClick={()=> handleClick("Agregar")} className="HomeMenuOption">
                    <h2>Agregar Personal</h2>
                </div>

                
                <div onClick={()=> handleClick("Quitar")} className="HomeMenuOption">
                    <h2>Quitar Personal</h2>
                </div>

                
                <div onClick={()=> handleClick("Editar")} className="HomeMenuOption">
                    <h2>Editar Personal</h2>
                </div>

            </div>
        )
        }else{
            alert("No tienes los permisos para entrar a esta pagina");
            window.location.href = "https://sistemaact.github.io/SistemaACTropico";
        }
    }else{
        if(props.user === undefined || props.user === "undefined"){
            alert("No tienes los permisos para entrar a esta pagina");
            window.location.href = "https://sistemaact.github.io/SistemaACTropico";

        }else{
            return(
                <React.Fragment>
                    <SubAppPersonal app={SubApp} data={Data}></SubAppPersonal>
                </React.Fragment>
            )
        }
    }

}

export default Personal